* {
  margin: 0;
  padding: 0;
  outline: none;
}
body {
  background-image: url("/public/background.jpg");
  background-color: rgb(87, 72, 72);
  background-attachment: fixed;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

button {
  border: 1px solid gray;
}

.popup {
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  width: 400px;
  height: 100%;
  background-color: aquamarine;
  z-index: 10;
}

.address-popup {
  height: 100%;
  width: 100%;
  container-type: inline-size;
  container-name: addressee-browser;
}

.contain-style {
  contain: style;
}

.contain-no-style {
  contain: none;
}

.hidden {
  display: none !important;
}

.flex {
  display: flex;
}

.App {
  width: 100%;
  height: 100%;
  display: contents;
}

.bodyContent {
  height: 100vh;
  width: 100%;
}

.page {
  background-color: antiquewhite;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  max-width: inherit;
  min-height: 0;
  z-index: 1;
  position: relative;
}